import React from 'react'
import BaseContainer from '../../../containers/BaseContainer'
import Table from '../../../components/Table'
import ApiService from '../../../service/ApiService'
import {alertMessage} from '../../../utils'
import { BrowserRouter, Route, Switch, Link, useRouteMatch } from 'react-router-dom';
import Edit from '../components/EditForm';


//const TABLE_DATA = []
var data = [];

class TableView extends BaseContainer {
  state={
    header: [{
      title:"#",
      prop: "no"
    }, {
      title:"Production Order",
      prop: "workorderNo"
    }, {
      title:"Order Type",
      prop:"plantCode"
    }, {
      title:"Material",
      prop:"plantCode"
    }, {
      title:"Material Description",
      prop:"plantCode"
    }, {
      title:"SKU",
      prop:"plantCode"
    }, {
      title:"Expected Prod. Date",
      prop:"plantCode"
    }, {
      title:"Expected Case",
      prop:"plantCode"
    }, {
      title:"Expected Pallete",
      prop:"plantCode"
    }, {
      title:"Status",
      prop:"plantCode"
    }, {
      title:"Line",
      prop:"plantCode"
    }, {
      title:"Action"
    }],
    data: data
  }

  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(i) {
    alert('Click happened' + i);
  }

  onClick(i) {
    alert('Click happened' + i);
  }

  componentWillMount() {
    this.setState({
      title: "Production Orders"
    })
    ApiService.fetchCcaProcessOrders()
      .then(response => {
        this.setState({ 
          data: response.data 
        });
      })
      this.showLoading(true)
        setTimeout(() => {
          this.showLoading(false)
        }, 500)

    if(data == null || data == undefined) {
      this.showLoading(true)
    } else {
      setTimeout(() => {
        this.showLoading(false)
      }, 500)
    }
  }



  _renderContent() {
    console.log(this.state)
    return <div className="portlet body">
      <Table
        searchableProperties={["plantDescr","plantCode"]}
        hasTopBar={true}
        headers={this.state.header}
        data={this.state.data}
        rowRender={this._renderRow}
        onAddNewHandler={this.onAddNewClick.bind(this)}
      />
    </div>
  }

  onAddNewClick() {
    alert('OK')
  }
  
  _renderRow(itm, i) {
    return <tr key={i} className="odd gradeX">
      <td>{i+1}</td>
      <td></td>
      <td></td>
      <td></td>
      <td></td>
      <td></td>
      <td>{itm.expectedProdDate}</td>
      <td>{itm.expectedCase}</td>
      <td>{itm.expectedPallets}</td>
      <td>EOL</td>
      <td>-</td>
      <td>
        <div className="btn-group">
          <button className="btn btn-xs green dropdown-toggle" type="button" data-toggle="dropdown" aria-expanded="false"> Actions
            <i className="fa fa-angle-down"></i>
          </button>
          <ul className="dropdown-menu pull-left" role="menu">
            <li>
              <a href="javascript:;">
                <i className="icon-flag"></i> Area Name
                <span className="badge badge-success">4</span>
              </a>
            </li>
            <li>
              <a href="javascript:;">
                <i className="glyphicon glyphicon-tags"></i> Area Code </a>
            </li>
            <li className="divider"> </li>
            <li>
              <button onClick={() => this.handleClick(i)} >
                <i className="glyphicon glyphicon-trash"></i> Delete </button>
            </li>
          </ul>
        </div>
      </td>
    </tr>
  }
}

export default TableView



